
.markdown {
  position: relative;
  width: 100%;
  background: rgba(255, 255, 255, 0.4);
  padding-bottom: 7.2rem;
}
.markdown .title {
    font-size: 3.6rem;
    color: #5085A4;
    padding: 7.2rem 0 2.88rem;
}
.markdown .center {
    max-width: 750px;
    margin: auto;
}
.md-display {
  font-size: 1.6rem;
  font-weight: 300;
  letter-spacing: .8px;
  line-height: 1.5;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  text-align: left;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  color: black;
  background: white;
  min-height: 400px;
  padding: 2.88rem 3.6rem;
  word-break: break-all;
}
.md-display h1 {
    color: #5085A4;
    line-height: 1.2;
}
.md-display h2 {
    color: #284352;
}
.md-display p {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
.md-display pre, .md-display blockquote {
    background: rgba(122, 122, 122, 0.05);
    padding: 1.6rem;
    letter-spacing: .4px;
    border: 1px solid rgba(80, 133, 164, 0.4);
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: .2rem;
}
.md-display blockquote {
    margin: 0;
    border-width: 0 0 0 4px;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}
.md-display h1, .md-display h2, .md-display h3, .md-display h4, .md-display h5, .md-display h6 {
    margin-top: .6em;
    margin-bottom: .4em;
}
.md-display pre, .md-display p, .md-display ul, .md-display ol, .md-display blockquote {
    margin-top: .5em;
    margin-bottom: .5em;
}
.md-display img {
    -ms-flex-item-align: center;
        align-self: center;
    max-width: 80%;
    max-height: 320px;
    border: 1px solid rgba(122, 122, 122, 0.2);
    border-radius: .2rem;
    margin: 0.8rem 0;
    cursor: -webkit-zoom-in;
    cursor: zoom-in;
}
.md-display a {
    opacity: .8;
}
#image-preview, #link-preview {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.9);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  cursor: -webkit-zoom-out;
  cursor: zoom-out;
}
#image-preview img, #image-preview iframe, #link-preview img, #link-preview iframe {
    background: #7a7a7a;
    -webkit-box-shadow: #7a7a7a 0 0 3rem -1rem;
            box-shadow: #7a7a7a 0 0 3rem -1rem;
}
#link-preview {
  cursor: w-resize;
}
#link-preview a {
    font-size: 1.6rem;
    line-height: 3;
    letter-spacing: .1rem;
}
.hljs,
.hljs-subst,
.hljs-function,
.hljs-title,
.hljs-params,
.hljs-formula {
  color: #688392;
}



