
html {
  background-color: rgba(80, 133, 164, 0.1);
}
html, body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #5085A4;
  font-size: 10px;
}
::-moz-selection {
  background-color: #5085A4;
  color: #FFF;
}
::selection {
  background-color: #5085A4;
  color: #FFF;
}
@font-face {
  font-family: Krungthep;
  src: url(/static/font/Krungthep.ttf);
}
a {
  font-family: Krungthep;
  color: #5085A4;
  opacity: .4;
  -webkit-transition: all .3s;
  transition: all .3s;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
}
a:hover {
    opacity: 1;
}
#app {
  -webkit-animation: colorful 2s;
          animation: colorful 2s;
}
#app main {
    position: relative;
    top: 0;
}
@-webkit-keyframes colorful {
from {
    -webkit-filter: saturate(0);
            filter: saturate(0);
}
to {
    -webkit-filter: saturate(1);
            filter: saturate(1);
}
}
@keyframes colorful {
from {
    -webkit-filter: saturate(0);
            filter: saturate(0);
}
to {
    -webkit-filter: saturate(1);
            filter: saturate(1);
}
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
  opacity: 1;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}



